.img {
  color: $primary;
  font-size: .9em;
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 245px;
  small {
    position: absolute;
    display: block;
    top: 10px;
    left: 0;
    padding: 5px;
    line-height: 1em;
    background-color: $primary;
    color: $white;
    z-index: 100;
    &::after {
      content: '';
      width: 0; 
      height: 0; 
      border-top: 11px solid transparent;
      border-bottom: 11px solid transparent;
      border-left: 11px solid $primary;
      position: absolute;
      top: 0;
      right: -11px;
    }
  }
  a {
    display: block;
    position: relative;
    overflow: hidden;
    max-height: 245px;
    text-align: center;
    img {
      transition: all .3s;
      width: 100%;
    }
    &:hover {
      img {
        transform: scale(1.1);
      }   
    }
  }
}
.text-intor {
  p {
      word-break: break-all;
    }
  li {
    text-indent: -5em;
    padding-left: 5em;
    word-break: break-all;
    &::before{
      width:5em;
      display: inline-block;
      text-align: right;
      }
    &:first-child{
      &::before{
        content: '累積人氣：';
      }
    }
    &:nth-child(2){
      &::before{
        content: '角色名稱：';
      }
    }
    &:nth-child(3){
      &::before{
        content: 'PID：';
      }
    }
    &:nth-child(4){
      &::before{
        content: '伺服器：';
      }
    }
    &:last-child{
      &::before{
        content: '個性標籤：';
      }
    }
  }
}
.img-tit {
      font-size: 1.2em;
      text-align: center;
      position: relative;
      &::before {
        position: absolute;
        margin: auto;
        top: 0;
        bottom: 0;
        left: 1em;
        content: '';
        width: 20%;
        height: 1px;
        background-color: $primary;
      }
      &::after {
        position: absolute;
        margin: auto;
        top: 0;
        bottom: 0;
        right: 1em;
        content: '';
        width: 20%;
        height: 1px;
        background-color: $primary;
      }
}

